<template>
  <div class="telegram-mini-app">
    <h1>{{ title }}</h1>
    <button @click="login"></button>
    
    <textarea v-model="requestText" placeholder="Введите ваш запрос"></textarea>
    <button :disabled="buttonDisabled" @click="sendRequest">Отправить</button>
    <TransitionGroup name="fade" mode="out-in">
      <div v-for="res in response" class="response" :key="res">
        
          <p class="fade-element">{{ res }}</p>
        
      </div>
    </TransitionGroup>
  </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

export default {
  name: 'TelegramMiniApp',
  data() {
    return {
      title: "Telegram Mini App",
      requestText: "",
      buttonDisabled: false,
      response: [],
      responseKey: 0,
    };
  },
  methods: {
    login() {
      Telegram.Login.auth({bot_id: 5959040693, lang: 'en'}, function(user) {
        console.log(user)
      })
    },
    sendRequest() {
      // Отправка запроса
      this.buttonDisabled = true
      this.response.unshift(`${this.requestText} ${this.responseKey}`);
      this.responseKey += 1;
      this.requestText = ""
      this.buttonDisabled = false
    },
  },
};
</script>

