/* eslint-disable */
import { createStore } from 'vuex'

export default createStore({
  state() {
    return {
      requestText: '',
      response: [],
      responseKey: 0,
    };
  },
  mutations: {
    setRequestText(state, text) {
      state.requestText = text;
    },
    addResponse(state, response) {
      state.response.unshift(response);
      state.responseKey += 1;
    },
  },
  actions: {
    sendRequest({ commit, state }) {
      // Эмуляция отправки запроса
      const fakeResponse = 'Fake response for ' + state.requestText;
      commit('addResponse', fakeResponse);
    },
  },
  getters: {
    getRequestText: (state) => state.requestText,
    getResponse: (state) => state.response,
    getResponseKey: (state) => state.responseKey,
  },
});

